import {publicing} from '../../api/api.js'
import {searchurl} from '../../api/request.js'
// 引入反馈组件
import tips from '../../element/tips.vue'
// es6解构
var {log} = console
export default{
	components:{
		tips
	},
	data() {
		return {
			// 搜索关键词
			searchdata:'',
			// 搜索结果
			takeshop:[],
			// 搜索历史
			setdata:[],
			// 是否有搜索历史
			ifhistory:false,
			// 内容展示区
			shopcont:false
		}
	},
	
	
	methods:{
		// 点击搜索按钮触发的
		seArch(){
			log(this.searchdata)
			if(this.searchdata != ''){
				this.ifhistory = false
				let searchkey = this.searchdata
				this.searchData(searchkey)
				this.getStorage(searchkey)
			}
		},
		
		// 搜索键盘的触发
		onKeyInput(e){
			log(e.detail.value)
			let searchkey = e.detail.value
			if(searchkey != ''){
				this.ifhistory = false
				this.searchData(searchkey)
				this.getStorage(searchkey)
			}
		},
		
		// 本地存入搜索历史
		getStorage(searchkey){
			// 先取出本地缓存的数据
			let seararray = uni.getStorageSync('search_key') || []
			// 要存入一个数组array
			seararray.unshift(searchkey)
			uni.setStorageSync('search_key', seararray);
		},
		
		// 取出本地缓存的数据
		setStorage(){
			let setdata = uni.getStorageSync('search_key')
			// 数组去重
			let setdataarr = Array.from(new Set(setdata))
			// log(setdataarr)
			if(setdataarr == ''){
				this.ifhistory = false
			}else{
				this.setdata = setdataarr
				this.ifhistory = true
			}
		},
		
		// 手动清除搜索历史
		removeStorage(){
			uni.removeStorageSync('search_key');
			this.setStorage()
		},
		
		// 搜索历史的搜索
		menubtn(item){
			// log(item)
			this.ifhistory = false
			this.searchData(item)
		},
		
		
		// 发送请求搜索
		searchData(searchkey){
			let data = {
				searchdata:searchkey
			}
			publicing(searchurl,data)
			.then((res)=>{
				log(res)
				if(res.data == '没有商品数据'){
					log('没有商品数据')
					this.shopcont = false
					let bull = true
					let tips = '没有你要找的商品'
					this.tising(bull,tips)
				}else{
					let bull = false
					let tips = ''
					this.tising(bull,tips)
					this.shopcont = true
					this.takeshop = res.data
				}
				
			})
			.catch((err)=>{
				log(err)
			})
		},
		
		// 反馈提示
		tising(bull,tips){
			this.$nextTick(()=>{
				this.$refs.tips.init(bull,tips)
			})
		}
		
	},
	computed:{
		clearItem(){
			if(this.searchdata ==''){
				this.takeshop = []
			}
		}
	},
	
	created() {
		this.setStorage()
	}
	
	
}
